$colors:(black:#000000,white:#ffffff,gray:#C3C5C8);
$fonts:(families:(fantasy:(copperplate, fantasy),monospace:(courier-new, monospace),sans-serif:(arial, sans-serif),script:(brush-script-mt, script),serif:(times-new-roman, serif)),sizes:(xsmall:12px,small:14px,medium:16px,large:24px),weights:(light:400,bold:600));
$transitions:(fade:(slow:opacity 1s,medium:opacity .5s,fast:opacity .2s));
$sizes:(small:10px,medium:20px,large:40px);

 .default {
  background-color: #333;
  position: relative;
  flex: 1;
  cursor: pointer;

  // sizes
  &.small {
    margin: 17% 4%;
    flex-basis: calc(17% - 4%);
    min-width: calc(17% - 4%);
    min-height: calc(66% - 4% - 100px);
    max-height: calc(99% - 4% - 100px);
  }
  &.medium {
    margin: 17% 9%;
    flex-basis: calc(33% - 4%);
    min-width: calc(33% - 4%);
    min-height: calc(66% - 4% - 100px);
    max-height: calc(99% - 4% - 100px);
  }
  &.large {
    margin: 17% 4%;
    flex-basis: calc(99% - 4%);
    min-width: calc(99% - 4%);
    min-height: calc(66% - 4% - 100px);
    max-height: calc(99% - 4% - 100px);
  }

  a {
    width: 100%;
    height: 100%;
    position: absolute;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
  }

  name, description, price {
    display: none;
  }

  &:hover {
    opacity: 0.9;
    transition: .2s ease-in;

    thumbnail-mask {
      opacity: 0.7;
      transition: .2s ease-in;
      position: absolute;
      height: 33%;
      bottom: 0;
      background-color: black;
      width: 100%;
    }
    name {
      display: block;
      position: absolute;
      bottom: 0;
      left: 10;
      height: 23%;
      color: white;
    }
    price {
      display: block;
      position: absolute;
      bottom: 0;
      right: 10;
      height: 23%;
      color: white;
    }
    description {
      display: block;
      position: absolute;
      bottom: 0;
      left: 10;
      height: 17%;
      color: white;
    }
  }
}



